<template>
  <div id="app">
  <h1>梦想毁灭机！</h1>
  <!-- <p>请输入每月投入资金：</p>
    <input v-model="fund_monthly" placeholder="编辑我……"> -->

  <p>请输入投资年化（单位为%）：</p>
    <input v-model="interest_rate_yearly" placeholder="编辑我……">

  <p>请输入预计每年年收入增长率(单位为%)：</p>
    <input v-model="salary_increase_rate_yearly" placeholder="编辑我……">

  <p>请输入你预期十年后能拥有的资金总数：</p>
    <input v-model="goal" placeholder="编辑我……">

  <p>你需要的起始月薪至少为</p>
  <p> {{ base_salary }} 元！</p>

  <button v-on:click="dream_crash">梦想毁灭按钮！</button>

  <p>将就用着先，没时间优化了 /(ㄒoㄒ)/~~</p>

</div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
       fund_monthly: Number(10000),
       interest_rate_yearly: Number(12),
       salary_increase_rate_yearly: Number(25),
       goal: Number(30000000),
       base_salary: Number(0),
    }
  },
  // watch: {
  //
  //   fund_monthly: function (val) {
  //     this.base_salary = Number(val) + 1
  //   }
  // }，
  methods: {
    dream_crash: function (event) {
      // `this` 在方法里指向当前 Vue 实例
      this.base_salary = this.goal / (this.constant_cal(0) * this.s_cal(0))
    },
    constant_cal: function (val) {
      for (var i=0;i<10;i++){
        val += Math.pow((1 + this.salary_increase_rate_yearly/100), i) * Math.pow((1 + this.interest_rate_yearly/1200),(108-12*i))
        // val += (this.salary_increase_rate_yearly/100 + this.interest_rate_yearly/100)
      }
      return val
    },
    s_cal: function (val) {
      val = Math.pow((1 + this.interest_rate_yearly/1200), 11) - 1
      val = val / (this.interest_rate_yearly/1200)
      return val
    }

  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
button {
  font-size: 15px;
  margin-top: 10px;
  margin-bottom: 10px;
  border:2px #000 solid;
  background-color: #0000;

}
</style>



